import React, {PropTypes} from 'react';
import config from '../../config';
// import fetch from 'isomorphic-fetch';
import Paper from 'material-ui/Paper';
import {Grid, Row, Col} from 'react-flexbox-grid';
import SchemaForm from 'react-schema-form';

import {PageTemplate, Header, Footer} from 'components';

class FileDetailPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      id: null,
      schema: {},
      form: {}
    }
  }

  componentDidMount() {}

  componentWillMount() {
    this.setState({id: this.props.params.id})
    this.setState({

      "schema": {
        "type": "object",
        "title": "Comment",
        "properties": {
          "name": {
            "title": "Name",
            "type": "string",
            "default": "Steve"
          },
          "email": {
            "title": "Email",
            "type": "string",
            "pattern": "^\\S+@\\S+$",
            "validationMessage": "Email must be of proper format: example@example",
            "description": "Email will be used for evil."
          },
          "environment": {
            "type": "string",
            "title": "Environment",
            "enum": [
              "LOCAL",
              "SIT1",
              "SIT2",
              "SIT3",
              "UAT1",
              "UAT2"
            ]
          },
          "comment": {
            "title": "Comment",
            "type": "string",
            "maxLength": 20,
            "validationMessage": "Don't be greedy! 20 Characters max please :)",
            "description": "Please write your comment here."
          }
        },
        "required": ["name", "email", "comment"]
      },
      "form": [
        "name",
        "email",
        "environment", {
          "key": "comment",
          "type": "textarea",
          "placeholder": "Make a comment"
        }
      ]

    })

  }

  render() {

    return (
      <PageTemplate header={< Header />} footer={< Footer />}>
        {/* <SchemaForm schema={this.state.schema} form={this.state.form} model={this.state.model} onModelChange={this.onModelChange} mapper={mapper}/> */}
      </PageTemplate>
    )
  }
}

export default FileDetailPage;
